/*.magnific fix*/
.mfp-container {
    height: 100vh;
    overflow-y: auto;
    padding: 0;
    @media(max-width: 900px) {
        padding: 0;
    }
}

.mfp-bg {
    background: $project-overlay-base;
}

.mfp-close {
    position: absolute;
    top: 0;
    right: 0;
    width: 72px;
    height: 72px;
    font-size: 0;
    text-indent: -9999px;
    opacity: 1;
    @media (max-width: 768px) {
        top: 10px;
        right: 10px;
        width: 40px;
        height: 40px;
    }
    &::after {
        @include transition();
        @include containBg();
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 24px;
        height: 24px;
        background-image: url(../images/close-modal.svg);
        transform: translate(-50%, -50%);
    }
    &:hover {
        &::after {
            transform: translate(-50%, -50%) scale(1.1);
        }
    }
    &:active {
        top: 0;
        right: 0;
    }
}

.modal {
    position: relative;
    background: $white;
    width: calc(100% - 20px);
    max-width: 700px;
    margin: 10px auto;
    padding: 32px 74px 54px;
    @media (max-width: 1200px) {
        padding: 20px 60px 20px 40px;
    }
    @media (max-width: 768px) {
        padding: 20px;
    }
    .header-block {
        @media (max-width: 768px) {
            width: calc(100% - 40px);
        }
        &__text {
            font-size: 32px;
            @media (max-width: 768px) {
                font-size: 20px;
                &:before {
                    content: none;
                }
            }
        }
    }
    .input-group {
        margin-top: 36px;
        max-width: 400px;
        @media (max-width: 1200px) {
            margin-top: 25px;
        }
        @media (max-width: 768px) {
            margin-top: 15px;
        }
        &:first-of-type {
            margin-top: 0;
        }
    }
    &-form {
        margin-top: 24px;
        @media (max-width: 1200px) {
            margin-top: 15px;
        }
        @media (max-width: 768px) {
            margin-top: 10px;
        }
        &__policy {
            font-size: 1.4rem;
            line-height: 1.57;
            color: $gray;
            max-width: 450px;
            margin-top: 40px;
            @media (max-width: 1200px) {
                margin-top: 30px;
            }
            @media (max-width: 768px) {
                margin-top: 20px;
            }
            a {
                @include transition();
                color: $orange-dim;
                text-decoration: underline;
                &:hover {
                    color: $orange;
                }
            }
        }
        &__btn {
            margin-top: 24px;
            width: 200px;
            @media (max-width: 1200px) {
                width: 180px;
            }
            @media (max-width: 768px) {
                width: 160px;
            }
        }
    }
    &-success {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 3.8rem 0 2.6rem;
        .img {
            width: 100px;
            margin-bottom: 3.2rem;
            img {
                width: 100%;
            }
        }
        .title {
            font-weight: $font-weight--700;
            font-size: 2.2rem;
            line-height: 3.6rem;
            color: $base-color;
            text-transform: uppercase;
            text-align: center;
        }
    }
}
