.cookie-policy {
    display: none;
    position: fixed;
    z-index: 10;
    bottom: 0;
    left: 0;
    width: 100%;
    background: $cookie-bg;
    padding: 18px 0;
    @media (max-width: 768px) {
        padding: 15px 0;
    }
    .wrapper {
        max-width: 1854px;
    }

    &-row {
        display: flex;
        align-items: center;
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: center;
        }
    }
    &-text {
        font-size: 1.4rem;
        line-height: 1.57;
        color: $white;
    }
    &-button {
        width: 20rem;
        min-width: 20rem;
        margin-left: 26px;
        @media (max-width: 768px) {
            margin-top: 15px;
            margin-left: 0;
        }
        .btn {
            width: 100%;
        }
    }
}