.input-group {
    display: flex;
    flex-direction: column-reverse;
    &__title {
        font-size: 1.4rem;
        line-height: 1.57;
        color: $gray-middle;
        margin-bottom: 4px;
    }
    &__input {
        @include transition();
        font-size: 1.4rem;
        line-height: 1.57;
        color: $gray;
        border: 1px solid $gray-opacity-light;
        border-radius: 2px;
        padding: 11px 16px;
        &::placeholder {
            color: $gray-middle;
        }
        &:required {
            & + .input-group__title {
                &::after {
                    content: "*";
                    color: $orange-dim;
                    margin-bottom: 3px;
                    margin-left: 2px;
                }
            }
        }
        &:disabled {
            color: $gray-middle;
            border-color: $gray-opacity-light;
            background: $gray-light;
            pointer-events: none;
        }
        &:hover, &:focus {
            border-color: $gray;
        }
    }
    &__textarea {
        @include transition();
        font-size: 1.4rem;
        line-height: 1.57;
        color: $gray;
        height: 176px;
        border: 1px solid $gray-opacity-light;
        border-radius: 2px;
        padding: 12px 16px;
        resize: none;
        @media (max-width: 1200px) {
            height: 150px;
        }
        &::placeholder {
            color: $gray-middle;
        }
        &:required {
            & + .input-group__title {
                &::after {
                    content: "*";
                    color: $orange-dim;
                    margin-bottom: 3px;
                    margin-left: 2px;
                }
            }
        }
        &:disabled {
            color: $gray-middle;
            border-color: $gray-opacity-light;
            background: $gray-light;
            pointer-events: none;
        }
        &:hover, &:focus {
            border-color: $gray;
        }
    }
    ul {
        list-style: none;
        color: $red;
        font-size: 1.2rem;
        line-height: 1.66;
        margin-top: 4px;
        & + .input-group__input, & + .input-group__textarea {
            border-color: $red;
        }
    }
}

.required-fields__message {
    font-size: 1.4rem;
    line-height: 1.57;
    color: $gray;
    margin-bottom: 24px;
    @media (max-width: 1200px) {
        margin-bottom: 15px;
    }
    span {
        color: $orange-dim;
    }
}