.header-block {
    h1, h2, h3, h4, h5, h6 {
        font-size: 0;
    }

    &__text {
        display: flex;
        align-items: center;
        text-transform: uppercase;
        font-weight: 700;
        color: $white;

        &::before {
            content: "";
            display: block;
            width: 4px;
            background: $orange-dim;
            margin-right: 20px;
            @media (max-width: 768px) {
                margin-right: 15px;
            }
        }

        &--1 {
            font-size: 3.6rem;
            line-height: 1.44;
            @media (max-width: 768px) {
                font-size: 20px;
            }
            &::before {
                height: 84px;
            }
        }

        &--2 {
            font-size: 3.2rem;
            line-height: 1.5;
            @media (max-width: 768px) {
                font-size: 18px;
            }
            &::before {
                height: 74px;
            }
        }

        &--3 {
            font-size: 2.4rem;
            line-height: 2;
            @media (max-width: 768px) {
                font-size: 18px;
            }
        }

        &--black {
            color: $gray;
        }
    }

    &--bottom {
        .header-block__text {
            position: relative;
            padding-bottom: 18px;
            &::before {
                content: "";
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 176px;
                height: 4px;
                background: $orange-dim;
                margin-right: 0;
            }
        }
    }
}