.footer {
    background: $gray;
    padding: 4.8rem 0 16px;
    @media (max-width: 992px) {
        padding-top: 30px;
    }
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-between;
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__logo {
        display: flex;
        width: 288px;
        @media (max-width: 1200px) {
            width: 230px;
        }
        @media (max-width: 992px) {
            width: 200px;
        }
        img {
            width: 100%;
        }
    }

    &-content {
        width: calc(100% - 288px - 24px);
        padding-left: 24px;
        max-width: 1226px;
        @media (max-width: 1200px) {
            width: calc(100% - 230px - 24px);
        }
        @media (max-width: 992px) {
            width: calc(100% - 200px - 24px);
        }
        @media (max-width: 768px) {
            width: 100%;
            max-width: 400px;
            margin-top: 10px;
            padding-left: 0;
        }
        &__bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 56px;
            @media (max-width: 1200px) {
                margin-top: 40px;
            }
            @media (max-width: 992px) {
                flex-direction: column;
                align-items: flex-start;
                margin-top: 30px;
            }
        }
    }

    &-nav {
        width: 100%;

        &__list {
            display: flex;
            flex-wrap: wrap;
            margin: 0 -39px;
            list-style: none;
            @media (max-width: 1200px) {
                margin: 0 -20px;
            }
            @media (max-width: 480px) {
                justify-content: center;
            }
        }

        &__item {
            margin: 10px 39px 0;
            @media (max-width: 1200px) {
                margin: 10px 20px 0;
            }
        }

        &__link {
            @include transition();
            font-size: 1.4rem;
            line-height: 1.57;
            font-weight: 700;
            text-transform: uppercase;
            color: $white;

            &.active {
                color: $orange;
            }

            &:hover {
                color: $orange-dim;
                text-decoration: none;
            }
        }
    }

    &-contacts {
        display: flex;
        justify-content: space-between;
        width: 568px;
        margin-right: 30px;
        @media (max-width: 1200px) {
            flex-direction: column;
            width: 300px;
        }
        @media (max-width: 992px) {
            flex-direction: row;
            width: 400px;
            margin-right: 0;
            margin-bottom: 20px;
        }
        @media (max-width: 480px) {
            flex-direction: column;
            align-items: center;
            max-width: 100%;
        }
        &__item {
            @include transition();
            display: flex;
            margin-right: 20px;
            font-size: 1.6rem;
            line-height: 1.5;
            font-weight: 600;
            color: $white;
            @media (max-width: 1200px) {
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
            @media(max-width: 992px) {
                margin-bottom: 0;
            }
            @media (max-width: 480px) {
                margin-right: 0;
                margin-bottom: 10px;
            }
            &:hover {
                text-decoration: none;
                color: $orange-dim;
            }
            &::before {
                @include containBg();
                content: "";
                display: block;
                width: 24px;
                height: 24px;
                margin-right: 4px;
            }
            &:last-child {
                margin-right: 0;
                @media (max-width: 480px) {
                    margin-bottom: 0;
                }
            }
            &--email {
                &::before {
                    background-image: url(../images/email-icon.svg);
                }
            }
            &--phone {
                &::before {
                    background-image: url(../images/phone-icon.svg);
                }
            }
        }
    }

    &-left {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 452px;
        @media (max-width: 1200px) {
            width: 360px;
        }
        @media (max-width: 992px) {
            width: 100%;
            max-width: 400px;
        }
        @media (max-width: 480px) {
            flex-direction: column;
        }
    }

    &-socials {
        display: flex;
        justify-content: space-between;
        margin-right: 20px;
        @media (max-width: 480px) {
            margin-right: 0;
            margin-bottom: 15px;
        }
        &__item {
            width: 24px;
            height: 24px;
            margin-right: 40px;
            &:last-child {
                margin-right: 0;
            }
            &:hover {
                path {
                    fill: $orange-dim;
                }
            }
            path {
                @include transition();
            }
        }
    }

    &__write-us {
        width: 200px;
        @media (max-width: 1200px) {
            width: 180px;
        }
        @media (max-width: 992px) {
            width: 160px;
        }
    }

    &-bottom {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-top: 84px;
        @media (max-width: 1200px) {
            margin-top: 50px;
        }
        @media (max-width: 992px) {
            margin-top: 30px;
        }
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: center;
            font-size: 14px;
        }
        &__policy {
            @include transition();
            color: $white;
            text-decoration: underline;
            @media (max-width: 768px) {
                margin-bottom: 10px;
            }
            &:hover {
                color: $orange-dim;
            }
        }
        &__coffee {
            color: $white;
            @media (max-width: 480px) {
                text-align: center;
            }
            a {
                @include transition();
                color: $orange-dim;
                &:hover {
                    text-decoration: none;
                    color: $orange;
                }
            }
        }
    }
}