html {
    @media (max-width: 1200px) {
        font-size: 9px;
    }
    @media (max-width: 992px) {
        font-size: 8.5px;
    }
    @media (max-width: 768px) {
        font-size: 7.5px;
    }
}

.container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow: hidden;
    & > .content {
        flex: 1 0 auto;
        position: relative;
    }
}

.wrapper {
    position: relative;
    width: 100%;
    min-width: map-get($wrapper, "min-width");
    max-width: map-get($wrapper, "max-width");
    margin: 0 auto;
    padding: map-get($wrapper, "padding");
}

.inner-page {

    .content {
        background-color: $gray;
        background-image: url("../images/inner-pages-bg.png");
        -webkit-background-size: cover;
        background-size: cover;
        background-position: top center;
        background-repeat: no-repeat;


        .header {
            position: static;

            &__wrapper {
                &::after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    width: calc(100% - 40px);
                    height: 1px;
                    background: $white-opacity-4;
                    transform: translateX(-50%);
                }
            }
        }
    }

    &--error {
        .content {
            background-image: url("../images/404-bg.png");
        }
    }
}