.header {
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;

    &__wrapper {
        display: flex;
        justify-content: space-between;
        padding-top: 16px;
        padding-bottom: 24px;
        @media (max-width: 1200px) {
            align-items: center;
            padding-top: 15px;
            padding-bottom: 20px;
        }
        @media (max-width: 768px) {
            padding: 15px 20px;
        }
    }

    &-logo {
        display: flex;
        align-self: flex-end;
        width: 288px;
        @media (max-width: 1200px) {
            width: 200px;
        }
        @media (max-width: 992px) {
            position: absolute;
            top: 15px;
            left: 20px;
            width: 150px;
        }
        @media (max-width: 768px) {
            position: static;
            width: 130px;
        }
        img {
            width: 100%;
        }
    }

    &-content {
        width: calc(100% - 288px);
        padding-left: 24px;
        max-width: 1226px;
        @media (max-width: 1200px) {
            width: calc(100% - 200px);
        }
        @media (max-width: 992px) {
            width: 100%;
            padding-left: 0;

        }
        @media (max-width: 768px) {
            @include transition();
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            position: fixed;
            z-index: 10;
            top: 0;
            right: 0;
            width: 100%;
            height: 100vh;
            background: rgba(255, 255, 255, .8);
            transform: translateX(100%);
            &.active {
                transform: translateX(0);
            }
        }
        &__top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width: 992px) {
                padding-left: 170px;
            }
            @media (max-width: 768px) {
                flex-direction: column;
                flex-grow: 1;
                justify-content: flex-start;
                order: 1;
                width: 320px;
                padding-left: 0;
                background-color: $white;
            }
        }

        &__bottom {
            margin-top: 32px;
            @media (max-width: 1200px) {
                margin-top: 15px;
            }
            @media (max-width: 768px) {
                width: 320px;
                margin-top: 0;
                padding-top: 15px;
                background-color: $white;
            }
        }
    }

    &-contacts {
        display: flex;
        justify-content: space-between;
        width: 568px;
        margin-right: 20px;
        @media (max-width: 992px) {
            flex-direction: column;
            width: 380px;
        }
        @media (max-width: 768px) {
            width: auto;
            margin-top: 25px;
            margin-right: 0;
        }
        &__item {
            @include transition();
            display: flex;
            align-items: center;
            font-size: 1.6rem;
            line-height: 1.5;
            font-weight: 600;
            color: $white;
            @media (max-width: 768px) {
                color: $base-color;
            }
            &:hover {
                text-decoration: none;
                color: $orange-dim;
            }

            &::before {
                @include containBg();
                content: "";
                display: block;
                width: 24px;
                height: 24px;
                margin-right: 4px;
            }

            &--email {
                &::before {
                    background-image: url(../images/email-icon.svg);
                }
            }

            &--phone {
                &::before {
                    background-image: url(../images/phone-icon.svg);
                }
            }
        }
    }

    &-right {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 452px;
        @media (max-width: 768px) {
            flex-direction: column;
            width: auto;
        }
    }

    &-socials {
        display: flex;
        justify-content: space-between;
        margin-right: 20px;
        @media (max-width: 768px) {
            margin-top: 25px;
            margin-right: 0;
        }
        &__item {
            width: 24px;
            height: 24px;
            margin-right: 40px;
            @media (max-width: 1200px) {
                margin-right: 25px;
            }
            @media (max-width: 768px) {
                path {
                    fill: $base-color;
                }
            }
            &:last-child {
                margin-right: 0;
            }

            &:hover {
                path {
                    fill: $orange-dim;
                }
            }

            path {
                @include transition();
            }
        }
    }

    &-burger {
        display: none;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        @media (max-width: 768px) {
            display: flex;
        }
        &--close {
            display: none;
            position: absolute;
            top: 20px;
            right: 20px;
            @media (max-width: 768px) {
                display: block;
            }
        }
    }

    &-nav {
        &__list {
            display: flex;
            flex-wrap: wrap;
            margin: 0 -39px;
            list-style: none;
            @media (max-width: 1200px) {
                margin: 0 -30px;
            }
            @media (max-width: 768px) {
                flex-direction: column;
                align-items: center;
            }
        }

        &__item {
            margin: 10px 39px 0;
            @media (max-width: 1200px) {
                margin: 7px 30px 0;
            }
        }

        &__link {
            @include transition();
            font-size: 1.4rem;
            line-height: 1.57;
            font-weight: 700;
            text-transform: uppercase;
            color: $white;
            @media (max-width: 768px) {
                color: $base-color;
                font-size: 14px;
            }
            &.active {
                color: $orange;
            }

            &:hover {
                color: $orange-dim;
                text-decoration: none;
            }
        }
    }

    &__write-us {
        width: 200px;
        svg {
            display: none;
        }
        @media (max-width: 1200px) {
            width: 180px;
        }
        @media (max-width: 992px) {
            width: 170px;
        }
        @media (max-width: 768px) {
            width: 130px;
            margin-top: 25px;
        }
    }
}