.btn {
    @include transition();
    display: inline-block;
    font-size: 1.6rem;
    line-height: 1.5;
    font-weight: 600;
    border: 2px solid;
    padding: 14px;
    text-align: center;
    background-color: transparent;
    @media (max-width: 1200px) {
        padding: 12px;
    }
    &:hover {
        text-decoration: none;
    }
    &--orange {
        color: $orange-dim;
        border-color: $orange-dim;
        &:hover {
            color: $white;
            border-color: $white;
        }
    }
    &--orange-fill {
        color: $white;
        background: $orange-dim;
        border-color: $orange-dim;
        &:hover {
            background: $orange;
            border-color: $orange;
        }
    }
}